.statistics {
  width: 335px;
  margin: auto;
  border: 2px solid gray;
  border-radius: 10px;
}

.title {
  font-size: 16p;
  color: #333333;
  text-align: center;
  margin-top: 30px;
  margin-bottom: 25px;
  margin-bottom: 20px;
}

.stat_list {
  height: 60px;
  display: flex;
  list-style: none;
  padding-left: 50px;
  padding-right: 50px;
}

.item {
  width: 100px;
  border: solid 1px teal;
}

.label {
  margin-top: 5px;
  margin-bottom: 5px;
  display: flex;
  justify-content: space-evenly;
}

.percentage {
  font-size: 16px;
  display: flex;
  justify-content: space-evenly;
}
